import * as React from "react";
import * as PropTypes from "prop-types";

export interface RadioGroupProps {
    /**
     * Name of the field
     */
    name?: string;

    /**
     * The currently selected value
     */
    selectedValue?: string | number | boolean;

    /**
     * Triggered on any change
     */
    onChange?: (value: string | number, event: React.ChangeEvent<HTMLInputElement>) => void;

    /**
     * Disables the switch
     */
    Component?: string | React.ComponentClass<any> | React.SFC<any>;
}

export interface RadioGroupContext {
    radioGroup: {
        /**
         * Name of the field
         */
        name?: string;

        /**
         * The currently selected value
         */
        selectedValue?: string | number | boolean;

        /**
         * Triggered on any change
         */
        onChange?: (value: string | number, event: React.ChangeEvent<HTMLInputElement>) => void;
    };
}

export default class RadioGroup extends React.Component<RadioGroupProps, {}> {
    public static displayName = "RadioGroup";

    public static defaultProps = {
        Component: "div"
    };

    public static childContextTypes = {
        radioGroup: PropTypes.object
    };

    public static propTypes = {
        name: PropTypes.string,
        selectedValue: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.number,
            PropTypes.bool,
        ]),
        onChange: PropTypes.func,
        children: PropTypes.node.isRequired,
        Component: PropTypes.oneOfType([
            PropTypes.string,
            PropTypes.func,
            PropTypes.object,
        ])
    };

    getChildContext() {
        return {
            radioGroup: {
                name: this.props.name,
                selectedValue: this.props.selectedValue,
                onChange: this.props.onChange
            }
        };
    }

    render() {
        const {Component, name, selectedValue, onChange, ...rest} = this.props;
        return <Component {...rest} />;
    }
}
